import { useCallback, useMemo } from 'react';
import useServerTimeOffset from './use-server-time-offset.js';
export default function useClientServerDateUtils() {
  const serverTimeOffset = useServerTimeOffset();
  const fromServerDateWithTimeOffset = useCallback(serverDate => ({
    serverDate,
    clientDate: new Date(serverDate.getTime() + serverTimeOffset)
  }), [serverTimeOffset]);
  const fromClientDateWithTimeOffset = useCallback(clientDate => ({
    clientDate,
    serverDate: new Date(clientDate.getTime() - serverTimeOffset)
  }), [serverTimeOffset]);
  return useMemo(() => ({
    fromServerDate: fromServerDateWithTimeOffset,
    fromClientDate: fromClientDateWithTimeOffset
  }), [fromServerDateWithTimeOffset, fromClientDateWithTimeOffset]);
}